import {PinchZoom} from '@/assets/pinchzoom.js';


export default{
   data() {
      return {
        "imgUrl" :""
      }
   },

   created() {
      let id=this.$route.params.id;
      let type=this.$route.params.type;
      var url=_baseUrl +"/bpm/activiti/processImage.do?";
      switch(type){
      	case "task":
      		url+="taskId=" ;
      		break;
      	case "inst":
      		url+="instId=" ; 
      		break;
      	case "start":
      		url+="actDefId=" ; 
      		break;
      }
      //url+=(type=="task") ? "taskId=" : "instId=";
      url+=id;
      this.imgUrl=url;
      
      this.$dialog.loading.open(',...');
      
   },
   mounted(){
   		let el = document.getElementById('processImg');
			let pz = new PinchZoom(el);
			
			el.onload =()=>{
				this.$dialog.loading.close();
			}
			
   }
}



